















.dokan-dashboard .widgets-wrapper {
  display: block;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
}
.dokan-dashboard .widgets-wrapper .left-side,
.dokan-dashboard .widgets-wrapper .right-side {
  float: left;
  width: 48%;
}
.dokan-dashboard .widgets-wrapper .left-side {
  margin-right: 3%;
}
.dokan-dashboard .dokan-postbox .loading {
  display: block;
  width: 100%;
  margin: 15px auto;
  text-align: center;
}

.withdraw-requests .image {
  width: 10%;
}
.withdraw-requests .seller {
  width: 20%;
}
.withdraw-requests td.seller img {
  float: left;
  margin-right: 10px;
  margin-top: 1px;
  width: 24px;
  height: auto;
}
.withdraw-requests td.seller strong {
  display: block;
  margin-bottom: .2em;
  font-size: 14px;
}
.withdraw-requests td.status span {
  line-height: 2.5em;
  padding: 5px 8px;
  border-radius: 4px;
}
.withdraw-requests td.status .approved {
  background: #c6e1c6;
  color: #5b841b;
}
.withdraw-requests td.status .pending {
  background: #f8dda7;
  color: #94660c;
}
.withdraw-requests td.status .cancelled {
  background: #eba3a3;
  color: #761919;
}

.dokan-pro-features .pro-feature-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;
}
.dokan-pro-features .pro-feature {
  min-width: 48%;
  max-width: 48%;
  margin-bottom: 20px;
  border: 1px solid #E6E6E6;
  background: #fff;
}
@media screen and (max-width: 782px) {
.dokan-pro-features .pro-feature {
    min-width: 100%;
    max-width: 100%;
}
}
.dokan-pro-features .pro-feature:nth-child(2n) {
  margin-right: 0px;
}
.dokan-pro-features .pro-feature-thumb img {
  width: 100%;
  height: auto;
}
.dokan-pro-features .pro-detail {
  padding: 10px 20px;
  min-height: 100px;
  background: #fff;
}
.dokan-pro-features .pro-detail h3.title {
  margin: 5px 0 10px;
  padding: 0;
}
.dokan-pro-features .pro-detail h3.title a {
  text-decoration: none;
  color: #111;
}
.dokan-pro-features .pro-links {
  padding: 10px;
  background: #F5F5F5;
  border-top: 1px solid #E6E6E6;
}
.dokan-pro-features a.button.disabled {
  background: #eee;
}
